<template>
  <v-container
    fluid
  >
    <v-row>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="12" sm="10">
      <h2>Settings</h2>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="6" sm="5">
        <span>Dark Mode</span>
      </v-col>
      <v-col class="right" cols="6" sm="5">
        <v-switch
          v-model="$vuetify.theme.dark"
          hide-details
          inset
        ></v-switch>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="7" sm="6">
        <span>Temperature Units</span>
      </v-col>
      <v-col class="right" cols="5" sm="4">
        <v-select
          outlined
          dense
          hide-details
          :items="['Farenheit', 'Celsius']"
          v-model="temperatureUnits"
        ></v-select>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

      <v-col cols="0" sm="1"></v-col>
      <v-col cols="7" sm="6">
        <span>Elevation Units</span>
      </v-col>
      <v-col class="right" cols="5" sm="4">
        <v-select
          outlined
          dense
          hide-details
          :items="['Feet', 'Meters']"
          v-model="elevationUnits"
        ></v-select>
      </v-col>
      <v-col cols="0" sm="1"></v-col>

    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'Settings',

  computed: {

    temperatureUnits: {
      get () {
        return this.$store.state.temperatureUnits
      },
      set (value) {
        this.$store.commit('setTemperatureUnits', value)
      }
    },

    elevationUnits: {
      get () {
        return this.$store.state.elevationUnits
      },
      set (value) {
        this.$store.commit('setElevationUnits', value)
      }
    }

  }
}
</script>

<style scoped>
  .col-sm-1 {
    padding: 0;
  }
  .col, .col-sm-5 {
    display: flex;
    align-items: center;
  }
  .right {
    flex-direction: row-reverse;
  }
  .v-input {
    margin: 0;
  }
</style>
